
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #afb6bb;
}

.container {

}

.headerVirus {
    text-align: center;
    font-family: Tahoma, sans-serif;
    margin-top: 50px;
    margin-bottom: 30px;
    color: #591f1f;
    font-family: Roboto, sans-serif;
    font-size: 50px;


}

.poland_header {
    text-align: center;
    margin-bottom: 40px;
    color: #2e2e2e;
    border-bottom: 2px solid #494949;
    font-family: Roboto, sans-serif;

}

.world_header {
    text-align: center;
    margin-bottom: 40px;
    color: #2e2e2e;
    border-bottom: 2px solid #494949;
    font-family: Roboto, sans-serif;




}

.map_and_chart {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    padding-bottom: 20px;
    border-bottom: 3px solid #494949;


}


.map {
    margin-left: 50px;
    width: 712px;
    height: 491px;

}

.chart {
    width: 400px;

}

.flex {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;

}

.dropdown {

    border: 2px solid #dbdbdb;
    background-color: #d1d1d1;
    color: #2e2e2e;
    height: 30px;
    font-size: 16px;
    font-family: Roboto, sans-serif;


}


.confirmed {
    width: 300px;
    height: 150px;
    padding: 0.5em;
    border: 1px solid #ff5a5a;
    background: #ffa8a8;
    border-radius: 20px;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #2e2e2e;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    flex-direction: column;
    font-family: Roboto, sans-serif;

}

.recovered {
    width: 300px;
    height: 150px;
    padding: 0.5em;
    border: 1px solid #b7ff99;
    background: #d8ffc8;
    border-radius: 20px;
    text-align: center;
    margin-bottom: 20px;
    color: #2e2e2e;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    flex-direction: column;
    font-family: Roboto, sans-serif;



}

.deaths {
    width: 300px;
    height: 150px;
    padding: 0.5em;
    border: 1px solid #556463;
    background: #95a6a4;
    border-radius: 20px;
    text-align: center;
    color: #2e2e2e;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    flex-direction: column;
    font-family: Roboto, sans-serif;


}

.update {
    margin-left: 68px;
    margin-top: 30px;
    display: block;
    border: 2px solid #e50000;
    width: 400px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-family: Roboto, sans-serif;


}

@media screen and (max-width: 800px){


    .flex {
        display: flex;
        flex-direction: column;

    }


    .box {
        padding: 2em 1em;
        max-width: 300px;
        line-height: 1.3;
    }

}